@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Montserrat";

  font-display: swap;
  src:
    url("../fonts/montserratregular.woff2") format("woff2"),
    url("../fonts/montserratregular.woff") format("woff");
}

